import { _decorator, Component, Sprite, SpriteFrame } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('MahjongTile')
export class MahjongTile extends Component {
    @property(Sprite)
    background: Sprite = null!;  // 绑定Background节点

    @property(Sprite)
    face: Sprite = null!;        // 绑定Face节点

    // 动态设置牌面
    setTile(type: string, number: string) {
        // 加载牌面资源
        const path = `Tiles/mj_${type}_${number}`;
        resource.load(path, SpriteFrame, (err, spriteFrame) => {
            if (err) {
                console.error('加载牌面失败:', path);
                return;
            }
            this.face.spriteFrame = spriteFrame;
        });
    }
}